import React, { Component } from 'react';
import PropTypes from 'prop-types';
import axios from 'axios';
import { url } from 'config';

class CreateGoal extends Component {
  state = {
    scoringPlayerName: '',
    opposingTeamName: '',
    minute: '',
    ourTeamScored: true,
    submitError: false
  }

  handleChange = ({ target }) => {
    this.setState({ [target.name]: target.value })
  }

  handleToggleChange = ({ target }) => {
    const parsedValue = target.value === 'true' ? true : false;
    this.setState({ [target.name]: parsedValue })
  }

  handleSubmit = (e) => {
    e.preventDefault()
    this.setState({ submitError: false });

    axios({
      method: 'post',
      url: `${url}/goals`,
      headers: {
        'User-Name': this.props.userName,
        'Remember-Token': this.props.rememberToken
      },
      data: {
        scoring_player_name: this.state.scoringPlayerName,
        opposing_team_name: this.state.opposingTeamName,
        minute: parseInt(this.state.minute, 10),
        our_team_scored: this.state.ourTeamScored,
      }
    })
    .then(({ data }) => {
      this.props.history.push(`goals/${data.id}`)
    })
    .catch(() => {
      this.setState({ submitError: true });
    })
  };

  renderError() {
    if (!this.state.submitError) return null;

    return (
      <p>There was an error submitting your goal</p>
    )
  }

  render() {
    return (
      <form>
        <br/>
        <br/>
        {this.renderError()}
        <label>
          Scoring Player Name
          <input
            value={this.state.scoringPlayerName}
            onChange={this.handleChange}
            name='scoringPlayerName'
          />
        </label>
        <br/>

        <label>
          Opposing Team Name
          <input
            value={this.state.opposingTeamName}
            onChange={this.handleChange}
            name='opposingTeamName'
          />
        </label>
        <br/>

        <label>
          Minute
          <input
            value={this.state.minute}
            onChange={this.handleChange}
            name='minute'
          />
        </label>
        <br/>

        <label>
          Our Team Scored
          <input
            type="radio"
            name="ourTeamScored"
            value="true"
            checked={this.state.ourTeamScored}
            onChange={this.handleToggleChange}
          />
        </label>

        <label>
          Opposing Team Scored
          <input
            type="radio"
            name="ourTeamScored"
            value="false"
            checked={!this.state.ourTeamScored}
            onChange={this.handleToggleChange}
          />
        </label>
        <br />

        <button onClick={this.handleSubmit}>Submit</button>
      </form>
    );
  }
}

CreateGoal.propTypes = {
  userName: PropTypes.string.isRequired,
  rememberToken: PropTypes.string.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired
  })
};

export default CreateGoal;



