import React, { Component, Fragment } from 'react';
import { Route } from 'react-router-dom';
import { MemoryRouter } from 'react-router';
import axios from 'axios';
import { url } from 'config';
import CreateGoal from 'CreateGoal';
import ShowGoal from 'ShowGoal';

class App extends Component {
  state = {
    logInName: "",
    logInPassword: ""
  }

  componentDidMount() {
    this.setState({
      sessionName: sessionStorage.getItem('name'),
      sessionRememberToken: sessionStorage.getItem('rememberToken')
    });
  }

  handleNameChange = ({ target }) => {
    this.setState({ logInName: target.value });
  }

  handlePasswordChange = ({ target }) => {
    this.setState({ logInPassword: target.value });
  }

  loggedIn = () => {
    return this.state.sessionName && this.state.sessionRememberToken;
  }

  submitForm = (e) => {
    e.preventDefault();

    this.setState({ logInError: false });

    axios.post(`${url}/sessions`, {
      name: this.state.logInName,
      password: this.state.logInPassword,
    })
    .then(({ data }) => {
      this.setState({
        sessionName: data.name,
        sessionRememberToken: data.token
      });

      sessionStorage.setItem('name', data.name);
      sessionStorage.setItem('rememberToken', data.token);
    })
    .catch(() => {
      this.setState({ logInError: true });
    })
  }

  renderErrorMessage() {
    if (!this.state.logInError) return null;

    return <p>Error Logging In</p>;

  }

  renderLogInForm() {
    return (
      <form>
        {this.renderErrorMessage()}

        <h1>Log In</h1>

        <label>
          Name
          <input
            value={this.state.logInName}
            onChange={this.handleNameChange}
          />
        </label>

        <label>
          password
          <input
            value={this.state.logInPassword}
            onChange={this.handlePasswordChange}
            type="password"
          />
        </label>
        <button onClick={this.submitForm}>
          Submit
        </button>
      </form>
    )
  }

  logOut = () => {
    this.setState({
      sessionName: null,
      sessionRememberToken: null
    });
  }

  renderShowGoal = ({ match, history }) =>
    <ShowGoal
      userName={this.state.sessionName}
      rememberToken={this.state.sessionRememberToken}
      match={match}
      history={history}
    />

  renderCreateGoal = ({ history }) =>
    <CreateGoal
      userName={this.state.sessionName}
      rememberToken={this.state.sessionRememberToken}
      history={history}
    />

  render() {
    if (this.loggedIn()) {
      return (
        <Fragment>
          <p>Logged In As: {this.state.sessionName}</p>
          <button onClick={this.logOut}>Log Out</button>

          <MemoryRouter>
            <React.Fragment>
              <Route
                exact
                path={'/'}
                render={this.renderCreateGoal}
              />
              <Route
                exact
                path={`/goals/:id`}
                render={this.renderShowGoal}
              />
            </React.Fragment>
          </MemoryRouter>
        </Fragment>
      )
    }

    return (
      <div>
        {this.renderLogInForm()}
      </div>
    );
  }
}

export default App;



