import React, { Component } from 'react';
import PropTypes from 'prop-types';
import axios from 'axios';
import { url } from 'config';

class ShowGoal extends Component {
  state = {
    getGoalError: false,
    sendToTwitterError: false,
    data: { posted_to_twitter: false }
  }

  componentDidMount() {
    axios({
      method: 'get',
      url: `${url}/goals/${this.props.match.params.id}`,
      headers: {
        'User-Name': this.props.userName,
        'Remember-Token': this.props.rememberToken
      }
    })
    .then(({ data }) => {
      this.setState({ data })
    })
    .catch(() => {
      this.setState({ getGoalError: true });
    })
    .then(() => {
      if (!this.state.data.posted_to_twitter) {
        axios({
          method: 'post',
          url: `${url}/goals/${this.props.match.params.id}/post-to-twitter`,
          headers: {
            'User-Name': this.props.userName,
            'Remember-Token': this.props.rememberToken
          }
        })
        .then(({ data }) => {
          this.setState({ data })
        })
        .catch(() => {
          this.setState({ sendToTwitterError: true });
        })
      }
    })
  }

  displayError() {
    if (this.state.sendToTwitterError) return <p>Error submitting to twitter</p>;
    if (this.state.getGoalERror) return <p>Error getting goal</p>;
    return null;
  }

  render() {
    const { data } = this.state;

    return (
      <div>
        {this.displayError()}

        <p>Scoring Player Name: {data.scoring_player_name}</p>
        <p>Opposing Team Name: {data.opposing_team_name}</p>
        <p>Minute: {data.minute}</p>
        <p>Our Team Scored: {String(data.our_team_scored)}</p>
        <p>Posted to Twitter: {String(data.posted_to_twitter)}</p>

        <br />

        <button onClick={() => this.props.history.push('/')}>
          Submit Another Goal
        </button>
      </div>
    );
  }
}

ShowGoal.propTypes = {
  userName: PropTypes.string.isRequired,
  rememberToken: PropTypes.string.isRequired,
  match: PropTypes.shape({
    params: PropTypes.shape({ id: PropTypes.string.isRequired })
  }),
  history: PropTypes.shape({
    push: PropTypes.func.isRequired
  })
};

export default ShowGoal;



